
import FormatText from "../i18n"

let regexEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
let regexPhoneId = /^(^\+62\s?|^0)(\d{3,4}-?){2}\d{3,4}$/      // phone +62 or 08....
// let regexPhoneCountryCode = /^(\+)[\d]{6,16}$/    // phone with contry code
// let regexPhone = /^(\+62)[\d]{6,16}$/      // phone +62 
let regexLettersHypenUnderscore = /^([\.A-Za-z0-9_-]+)$/
let regexUri = /^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)?[a-z0-9]+([\-\.]{1}[a-z0-9]+)*[\:\.][a-z0-9]{2,5}(:[0-9]{1,5})?(\/.*)?$/
// let regexUriPort = /^https?:\/\/(?:w{1,3}\.)?[^\s.]+(?:\.[a-z]+)*(?::\d+)?(?:\/\w+(?:-\w+)*)*(?![^<]*(?:<\/\w+>|\/?>))/
let regexNumberOnly = /^[0-9]*$/
let regexStringOnly =/^[a-zA-Z ]*$/
let regexPath = /^(\/)?[a-zA-Z]+(\/.*)?$/

let result = {
  error: false,
  errorMessage: '',
}

export const FormValidation = (formType, value) => {
  switch (formType) {
    case 'otp':

      if (!regexNumberOnly.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_otp_must_number')
      }
      else if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_otp_empty')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "phone":
      // if (value.length === 0) {
      //   result.error = true
      //   result.errorMessage = FormatText('err_phone_empty')
      // }
      // else 
      if (!regexPhoneId.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_phone_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "name":
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_name_empty')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'clinicName':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_clinic_name_empty')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'address':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_address_empty')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "email":
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_email_empty')
      }
      else if (!regexEmail.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_email_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'password':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_password_empty')
      }
      else if (value.length < 8) {
        result.error = true
        result.errorMessage = FormatText('err_not_six_char')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'confirmPassword':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = "confirm password must be filled"
      }
      else if (value.length < 8) {
        result.error = true
        result.errorMessage = FormatText('err_not_six_char')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'clientId':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_clientid_empty')
      }
      else if (!regexLettersHypenUnderscore.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_clientid_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "clientName":
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_clientname_empty')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'roleId':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_roleid_empty')
      }
      else if (!regexLettersHypenUnderscore.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_roleid_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case 'initialRole':
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_initialrole_empty')
      }
      else if (!regexLettersHypenUnderscore.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_initialrole_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "clientType":
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_clienttype_not_choosen')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "profile":
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_profile_not_choosen')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result

    case "roleDescription":
      if (value.length === 0) {
        result.error = true
        result.errorMessage = FormatText('err_description_empty')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result
    case "originUris":
      if(value.length === 0){
        result.error = true
        result.errorMessage = FormatText('err_uri_empty')
      }
      else if (!regexUri.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_uri_format')
      }
      else {
        result.error = false
        result.errorMessage=""
      }
      return result
    case "redirectUris":
      if(value.length === 0){
        result.error = true
        result.errorMessage = FormatText('err_uri_empty')
      }
      else if (!regexUri.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_uri_format')
      }
      else {
        result.error = false
        result.errorMessage=""
      }
      return result
    case "emailSender":
      if (value.length === 0) {
        result.error = false
        result.errorMessage = ""
      }
      else if (!regexEmail.test(value)) {
        result.error = true
        result.errorMessage = FormatText('err_email_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result
    case "emailSenderMaskingName":

      if(value.length>100){
        result.error = true
        result.errorMessage = FormatText('err_masking_name_100')
      }
      else if (!regexStringOnly.test(value)){
        result.error = true
        result.errorMessage = FormatText('err_masking_name_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result
    case "path":
      if(value.length === 0){
        result.error = true
        result.errorMessage = FormatText('err_path_empty')
      }
      else if (!regexPath.test(value)){
        result.error = true
        result.errorMessage = FormatText('err_path_format')
      }
      else {
        result.error = false
        result.errorMessage = ""
      }
      return result
      case "urlDestination":
        if(value.length === 0){
          result.error = true
          result.errorMessage = FormatText('err_uri_empty')
        }
        else if (!regexUri.test(value)) {
          result.error = true
          result.errorMessage = FormatText('err_uri_format')
        }
        else {
          result.error = false
          result.errorMessage=""
        }
        return result
        case "clientDomain":
          if(value.length === 0){
            result.error = true
            result.errorMessage = FormatText('err_uri_empty')
          }
          else if (!regexUri.test(value)) {
            result.error = true
            result.errorMessage = FormatText('err_uri_format')
          }
          else {
            result.error = false
            result.errorMessage=""
          }
          return result
    default:
      return result
  }
}
