
import React, { useState, useEffect } from 'react';
import Typography from '@material-ui/core/Typography';
import Hidden from '@material-ui/core/Hidden';
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Link from '@material-ui/core/Link';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import MaterialTable from 'material-table';
import ReactExport from 'react-data-export';
import { yyyymmddWithParams, convertDateyyyymmdd } from "../../../utils/utils";
import FormatText from '../../../i18n';

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;

let from = yyyymmddWithParams('lastWeek')
let to = yyyymmddWithParams('nextYear')

const dateNow = new Date()
let lastWeek = new Date(dateNow.getTime() - 7 * 24 * 60 * 60 * 1000)
let nextWeek = new Date(dateNow.getTime() + 7 * 24 * 60 * 60 * 1000)
// let nextYear = new Date(dateNow.getTime() + 366 * 24 * 60 * 60 * 1000)

const datasetCreator = dataToProcess => {
  let columns = [
    { title: "OrderID", width: { wch: 7 } },
    { title: "Order Date", width: { wch: 12 } },
    { title: "Name", width: { wch: 20 } },
    { title: "Birth Date", width: { wch: 12 } },
    { title: "Status", width: { wch: 12 } },
    { title: "Clinic", width: { wch: 40 } },
    { title: "Redeem Date", width: { wch: 12 } },
    { title: "Redeem By", width: { wch: 12 } },
  ]

  let data = []
  dataToProcess.forEach(item => {
    let dataArr = []
    dataArr.push({ value: item.id })
    dataArr.push({ value: item.testdate })
    dataArr.push({ value: item.name })
    dataArr.push({ value: item.date_of_birth })
    dataArr.push({ value: item.status })
    dataArr.push({ value: item.klinikname })
    dataArr.push({ value: item.redeem_on })
    dataArr.push({ value: item.redeem_by })

    data.push(dataArr)
  })
  let dataset = [{ ySteps: 1, columns, data }]
  return dataset
}

const tableCreator = data => {
  let simpleTable = []
  data && data.forEach(order => order.items && order.items.forEach(item => {
    let rowTable = {
      id: order.id,
      testdate: item.products[0].testdate,
      name: item.name,
      date_of_birth: item.date_of_birth,
      status: item.status
        ? item.status.toUpperCase() === "FINISH" ? "REDEEMED" : item.status.toUpperCase()
        : "",
      klinikname: item.products[0].klinik.name,
      redeem_on: item.redeem ? convertDateyyyymmdd(item.redeem.redeem_on) : "-",
      redeem_by: item.redeem ? item.redeem.redeem_by : "-"
    }
    simpleTable.push(rowTable)
  }))
  return simpleTable
}


const SimpleTable = ({ ...props }) => {

  const { paxList, history, _setPaxList, _setOpenScreen, userInfo, _setCompleteRequest } = props

  const [state, setState] = useState({
    order: -1,
    "Booking Datefromdate": lastWeek,
    "Booking Datetodate": nextWeek,
    "Test Datefromdate": lastWeek,
    "Test Datetodate": nextWeek,
    "Redeem Datefromdate": lastWeek,
    "Redeem Datetodate": nextWeek,
    fromdate: lastWeek,
    todate: nextWeek,
    open: false,
    name: "",
    'Test Date': true,
    'Booking Date': false,
    'Redeem Date': false,
    Paid: false,
    New: false,
    finish: false,
    dataset: [],
    simpleTable: [],
    currentList: [],
    listLengthById: 0,
  })

  const tableColumns = [
    { title: "ID", field: 'id', },
    { title: FormatText('clinic_test_date'), field: 'testdate', },
    { title: FormatText('app_name'), field: 'name', },
    { title: FormatText('clinic_birth_date'), field: 'date_of_birth', },
    { title: 'Status', field: 'status', },
    { title: FormatText('clinic'), field: 'klinikname', },
    { title: FormatText('clinic_redeem_date'), field: 'redeem_on', },
    { title: FormatText('clinic_redeem_by'), field: 'redeem_by', },
  ]

  useEffect(() => {
    // console.log("EFFECT 1: ", paxList.list)
    let list = paxList.list
    let accessToken = localStorage.getItem('accessToken')
    if (accessToken) {
      _setOpenScreen(FormatText('clinic_order_list'))
      if (Array.isArray(list) && list.length > 0) {
        let simpleList = list.filter(item => (item.status !== "UNCONFIRMED") && (item.payment))
        let simpleTable = tableCreator(simpleList)
        let dataset = datasetCreator(simpleTable)
        setState(state => ({
          ...state,
          simpleTable,
          dataset,
          listLengthById: simpleList.length
        }))
      }
      else setState(state => ({
        ...state,
        simpleTable: [],
        dataset: [],
        listLengthById: 0
      }))
    }
    else {
      history.push('/')
    }
  }, [paxList.list, history, _setOpenScreen])

  useEffect(() => {
    // console.log("EFFECT 2")
    const infos = userInfo
    let name = infos.info.name
    let email = infos.info.email
    let phone = infos.info.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let healthOrderRequest = {
      request: {},
      method: "GET",
      serviceCode: "healthOrder",
      params: `/list?&testdate_from=${from}&testdate_to=${to}`,
      customHeaders
    }

    // let list = paxList.list
    // if (state.currentList !== list) 
    _setCompleteRequest(healthOrderRequest)

  }, [_setPaxList, _setCompleteRequest, userInfo])

  const handleRowClick = rowData => {
    console.log(rowData)
    const infos = userInfo
    let name = infos.info.name
    let email = infos.info.email
    let phone = infos.info.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let ID = rowData.id
    let healthOrderDetailRequest = {
      request: {},
      method: "GET",
      serviceCode: "HEALTH_ORDER_DETAIL",
      params: `/${ID}`,
      customHeaders
    }
    _setCompleteRequest(healthOrderDetailRequest)

    // let orderList = paxList.list
    // let order = orderList.filter(item => item.id === rowData.id)
    // props._setPaxDetail(order[0])
    // console.log(order[0])
    // history.push('/PaxDetail')
  }

  const handleClick = row => (event) => {
    let orderList = paxList.list
    let order = orderList.filter(item => item.id === row)
    console.log(order)
    props._setPaxDetail(order[0])
    if (order.length > 0) history.push('/PaxDetail')
  }

  const handleDateChange = prop => (date) => {
    setState({
      ...state,
      [prop]: date,
    })
  };

  const handleFilterOpen = () => {
    setState({
      ...state,
      order: 1,
      fromdate: lastWeek,
      todate: nextWeek,
      open: true,
      name: "",
      New: false,
      finish: false,
    })
  };

  const handleClose = () => {
    setState({
      ...state,
      open: false,
    })
  };

  const handleReset = () => {
    setState({
      ...state,
      order: 1,
      fromdate: lastWeek,
      todate: nextWeek,
      open: false,
      name: "",
      New: false,
      finish: false,
      '4': false,
      '5': false
      // localList: paxList.list,
    })
  };

  const handleChange = prop => event => {
    if (prop === 'Paid') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'New') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'finish') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Booking Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Test Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Redeem Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else {
      setState({
        ...state,
        [prop]: event.target.value,
      })
    }
  }

  // const handleKlinik = prop => event => {
  //   setState({
  //     ...state,
  //     [prop]: !state[prop],
  //   })
  // }

  const handleSubmitFilter = () => {
    const infos = props.userInfo
    // console.log("USER INFOOOOOO: ", infos)
    let name = infos.info.name ? infos.info.name : ''
    let email = infos.info.email ? infos.info.email : ''
    let phone = infos.info.phone ? infos.info.phone : ''

    // console.log("SUBMIT: ", state)
    // console.log(convertDateyyyymmdd(state.fromdate))

    // let from = convertDateyyyymmdd(state.fromdate)
    // let todate = convertDateyyyymmdd(state.todate)

    let payment_from = convertDateyyyymmdd(state['Booking Datefromdate'])
    let payment_to = convertDateyyyymmdd(state['Booking Datetodate'])
    let test_from = convertDateyyyymmdd(state['Test Datefromdate'])
    let test_to = convertDateyyyymmdd(state['Test Datetodate'])
    let redeem_from = convertDateyyyymmdd(state['Redeem Datefromdate'])
    let redeem_to = convertDateyyyymmdd(state['Redeem Datetodate'])

    let paymentParam = state['Booking Date'] ? `&paymentdate_from=${payment_from}&paymentdate_to=${payment_to}` : ""
    let testParam = state['Test Date'] ? `&testdate_from=${test_from}&testdate_to=${test_to}` : ""
    let redeemParam = state['Redeem Date'] ? `&redeemdate_from=${redeem_from}&redeemdate_to=${redeem_to}` : ""
    let statusParam = (state.Paid && !state.finish)
      ? '&status=PAID' //'&redeem_status=NEW'
      : (!state.Paid && state.finish)
        ? '&status=FINISH'   // '&redeem_status=REDEEMED'
        : (state.Paid && state.finish)
          ? ""
          : ""
    let nameParam = state.name ? `&name=${state.name}` : ''

    let isParams = paymentParam || testParam || redeemParam || statusParam || nameParam

    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    //  health/admin/orders?testdate_from=....&testdate_to=....redeem_status=NEW/REDEEMED
    let healthOrderRequest = {
      request: {},
      method: "GET",
      serviceCode: "healthOrder",
      params: isParams
        ? `/list?`
        + (paymentParam ? paymentParam : "")
        + (testParam ? testParam : "")
        + (redeemParam ? redeemParam : "")
        + (statusParam ? statusParam : "")
        + (nameParam ? nameParam : '')
        : `/list?&paymentdate_from=${payment_from}&paymentdate_to=${payment_to}`,
      customHeaders
    }
    props._setCompleteRequest(healthOrderRequest)
    // console.log(healthOrderRequest)
    setState({
      ...state,
      open: false,
    })
  }

  const handleExport = () => {
    // console.log("Export Clicked")
  }

  // const onRadioChange = e => {
  //   e.preventDefault()
  //   // console.log(e.target)
  //   setState({
  //     ...state,
  //     parent_id: parseInt(e.target.name)
  //   })
  // }


  let orderList = paxList.list
  let filteredList = paxList.filteredList
  let listToMap = filteredList[0] ? filteredList : orderList

  return (
    <div>

      {/* <Grid container direction="column" justify="center" alignItems="center">
          <Grid item>
            <Typography color="primary" variant="h6"  >
              <b>Total Order: {listToMap.length.toString()}</b>
            </Typography>
          </Grid>
          <Grid item>
            <Typography variant="caption" >Click list for detail</Typography>
          </Grid>
        </Grid> */}

      <Dialog
        // fullScreen={fullScreen}
        open={state.open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Filter by:"}</DialogTitle>
        <DialogContent>
          <Typography variant="subtitle2">{FormatText('clinic_test_request_date')}</Typography>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid container >
              <KeyboardDatePicker
                size="small"
                clearable
                format="yyyy/MM/dd"
                margin="normal"
                id="date-picker-from"
                label={FormatText('app_from')}
                value={state.fromdate}
                onChange={handleDateChange('fromdate')}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                }}
              />
            </Grid>
          </MuiPickersUtilsProvider>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid container >
              <KeyboardDatePicker
                clearable
                size="small"
                format="yyyy/MM/dd"
                margin="normal"
                id="date-picker-to"
                label={FormatText('app_to')}
                value={state.todate}
                onChange={handleDateChange('todate')}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                }}
              />
            </Grid>
          </MuiPickersUtilsProvider>
          <FormControl fullWidth>
            <FormGroup>
              <InputLabel
                error={state.emailErrorMessage ? true : false}
                htmlFor="standard-adornment-password">
                {FormatText('app_name')}
              </InputLabel>
              <Input
                error={state.emailErrorMessage ? true : false}
                id="standard-adornment-email"
                type='text'
                onChange={handleChange('name')}
              />
              {
                state.emailErrorMessage
                  ? <FormHelperText error id="email-helper-text">{state.emailErrorMessage}</FormHelperText>
                  : null
              }
            </FormGroup>
            <Typography style={{ marginTop: 15 }} variant="subtitle2">Status:</Typography>
            <FormControl>
              <FormControlLabel
                control={<Checkbox checked={state.New} onChange={handleChange('New')} name="New" />}
                label={FormatText('app_new')}
              />
              <FormControlLabel
                control={<Checkbox checked={state.finish} onChange={handleChange('finish')} name="finish" />}
                label="Redeemed"    // {FormatText('app_finish')}
              />
            </FormControl>
          </FormControl>

        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleReset} color="primary">
            Reset
          </Button>
          <Button autoFocus onClick={handleClose} color="primary">
            {FormatText('app_cancel')}
          </Button>
          <Button autoFocus onClick={handleSubmitFilter} color="primary">
            {FormatText('app_submit')}
          </Button>
        </DialogActions>
      </Dialog>

      <Hidden xsDown>
        <Grid container style={{ marginTop: 5 }}>
          {/* <Typography variant="subtitle1"  ><b>Filters</b> </Typography> */}
          <Grid container direction='row' justify='flex-start' alignItems='flex-start' style={{ marginBottom: 10, marginTop: 5 }} >
            {/* <Grid item sm={4} >
                <Grid item>
                  <Typography variant="subtitle2">Name:</Typography>
                  <FormControl style={{ width: 300 }}  >
                    <FormGroup >
                      <Input
                        error={state.emailErrorMessage ? true : false}
                        id="standard-full-width"
                        type='text'
                        placeholder='Name'
                        onChange={handleChange('name')}
                        endAdornment={
                          <InputAdornment position="end">
                            <AccountBoxRounded />
                          </InputAdornment>
                        }
                      />
                    </FormGroup>
                  </FormControl>
                </Grid>
                <Grid item> <br />
                </Grid>

                <Grid item>
                  <Typography style={{ marginBottom: 7 }} variant="subtitle2">Klinik Name:</Typography>
                  {
                    paxList.klinikList.length === 1
                      ? <TextField id="basic-klinik" value={paxList.klinikList[0].name} style={{ width: 300 }} />
                      : paxList.klinikList.length === 0
                        ? <TextField id="basic-klinik" disabled style={{ width: 300 }} />
                        : <SimpleAutocomplete
                          options={paxList.klinikList[0] ? paxList.klinikList : [{ name: '' }]}
                          onPick={v => console.log(v)}
                          width={300}
                          optionItem={'name'}
                        />
                  }

                </Grid>
              </Grid> */}
            <Grid item sm={state['Booking Date'] || state['Test Date'] || state['Redeem Date'] ? 6 : 3} >
              <Grid container direction="row" >
                <Typography variant='subtitle1'  >
                  Filter:
                  </Typography>

                <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                  <FormControl style={{ marginLeft: 25, marginTop: 2 }}  >
                    <Typography variant="subtitle2">Date:</Typography>
                    {
                      // ['Test Date', 'Booking Date', 'Redeem Date'].map((item, i) => {
                      ['Test Date', 'Redeem Date'].map((item, i) => {
                        return (
                          <Grid container direction="row" justify="flex-start" alignItems="center" key={i} >
                            <Grid item style={{ width: 150, marginRight: 15 }}>
                              <FormControlLabel
                                control={<Checkbox size='small' checked={state[item]} onChange={handleChange(item)} name={item} />}   //{<Radio />} 
                                label={item}
                              />
                            </Grid>
                            <Grid item style={{ display: state[item] ? "" : 'none' }} >
                              <MuiPickersUtilsProvider utils={DateFnsUtils} >
                                <Grid container >
                                  <KeyboardDatePicker
                                    style={{ width: 150, marginRight: 15 }}
                                    size="small"
                                    clearable
                                    format="yyyy/MM/dd"
                                    margin="dense"
                                    id={"date-picker-from" + item}
                                    label={FormatText('app_from')}
                                    value={state[item + 'fromdate']}
                                    onChange={handleDateChange(item + 'fromdate')}
                                    KeyboardButtonProps={{
                                      'aria-label': 'change date',
                                    }}
                                  />
                                </Grid>
                              </MuiPickersUtilsProvider>
                            </Grid>
                            <Grid item style={{ display: state[item] ? "" : 'none' }}>
                              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <Grid container >
                                  <KeyboardDatePicker
                                    style={{ width: 150, marginRight: 15 }}
                                    clearable
                                    size="small"
                                    format="yyyy/MM/dd"
                                    margin="dense"
                                    id={"date-picker-to" + item}
                                    label={FormatText('app_to')}
                                    value={state[item + 'todate']}
                                    onChange={handleDateChange(item + 'todate')}
                                    KeyboardButtonProps={{
                                      'aria-label': 'change date',
                                    }}
                                  />
                                </Grid>
                              </MuiPickersUtilsProvider>
                            </Grid>

                          </Grid>
                        )
                      })
                    }
                  </FormControl>
                </Grid>
              </Grid>
            </Grid>
            <Grid item sm={2} >
              <FormControl style={{ marginLeft: 15, marginTop: 30 }} fullWidth>
                <Typography variant="subtitle2">Status:</Typography>
                <FormControl>
                  <FormControlLabel
                    control={<Checkbox size='small' checked={state.Paid} onChange={handleChange('Paid')} name="Paid" />}
                    label={FormatText('app_paid')}
                  />
                  <FormControlLabel
                    control={<Checkbox size='small' checked={state.finish} onChange={handleChange('finish')} name="finish" />}
                    label="Redeemed"    //{FormatText('app_finish')}
                  />
                </FormControl>
              </FormControl>
            </Grid>
            <Grid item sm={2} >
              <Grid
                style={{ marginTop: 40 }}
                container
                direction="column"
                justify='center'
                alignItems="center">
                <Grid item>
                  <ExcelFile
                    filename={`OrderReport`} //</Grid></Grid>{`OrderReport ${ convertDateyyyymmdd(state.fromdate) } to ${convertDateyyyymmdd(state.todate)} `}
                    element={
                      <Button size='small' variant="outlined" color="primary">
                        {FormatText('app_export_xls')}
                      </Button>}
                  >
                    <ExcelSheet dataSet={state.dataset} name="Order Report" />
                  </ExcelFile>
                </Grid>
                <Grid item><br /></Grid>
                <Grid item>
                  <Button size='small' variant="contained" onClick={handleSubmitFilter} color="primary">
                    {FormatText('app_submit_filter')}
                  </Button>
                </Grid>
              </Grid>
            </Grid>
            {/* <Grid item sm={2} ></Grid> */}

          </Grid>
          <Grid item sm={12} container direction='row' justify='flex-start' alignItems='flex-start' style={{ marginBottom: 10, marginTop: 5 }} >

            <MaterialTable
              style={{ width: '100%' }}
              title={`TOTAL ORDER BY ID: ${state.listLengthById.toString()} `}
              columns={tableColumns}
              data={state.simpleTable}
              onRowClick={(e, rowData) => handleRowClick(rowData)}
              options={{
                padding: 'dense',
                // tableLayout: 'fixed'
                // showTitle: false,

              }}
            />
          </Grid>
          <Grid container>
            <div style={{ height: 20 }} />
          </Grid>
        </Grid>
      </Hidden>

      <Hidden smUp>
        <div>
          <Grid container direction="row" justify="space-between" alignItems="flex-start" style={{ marginTop: 10 }} >
            <Grid item>
              <Link onClick={handleFilterOpen} component="button" variant="subtitle1"><b>Filter</b> </Link>
            </Grid>
            <Grid item>
              <Link onClick={handleExport} component="button" variant="subtitle1"><b>Export</b> </Link>
            </Grid>
          </Grid>
          {listToMap.map((row, i) => (
            <div key={i}>
              <Card onClick={handleClick(row)} style={{ marginBottom: 10 }} >
                <CardContent>
                  <Link component="button" variant="subtitle1" style={{ marginBottom: 10 }}><b>OrderId: {row.id} </b> </Link>
                  {row.items ? row.items.map((item, j) => {
                    return (
                      <div key={j}>
                        <Grid container align='left'>
                          <Grid item lg={4} sm={4} xs={5}>
                            <Typography variant="caption" >{FormatText('app_name')}</Typography>
                          </Grid>
                          <Grid item lg={1} sm={1} xs={1} component='div'  >
                            <Typography >:</Typography>
                          </Grid>
                          <Grid item lg={7} sm={7} xs={6}   >
                            <Typography variant="caption" >{item.name}</Typography>
                          </Grid>
                        </Grid>
                        <Grid container align='left'>
                          <Grid item lg={4} sm={4} xs={5}>
                            <Typography variant="caption" >{FormatText('clinic_birth_date')}</Typography>
                          </Grid>
                          <Grid item lg={1} sm={1} xs={1} component='div'  >
                            <Typography >:</Typography>
                          </Grid>
                          <Grid item lg={7} sm={7} xs={6}   >
                            <Typography variant="caption" >{item.date_of_birth}</Typography>
                          </Grid>
                        </Grid>
                        <Grid container align='left'>
                          <Grid item lg={4} sm={4} xs={5}>
                            <Typography variant="caption"   >Status</Typography>
                          </Grid>
                          <Grid item lg={1} sm={1} xs={1} component='div'  >
                            <Typography >:</Typography>
                          </Grid>
                          <Grid item lg={7} sm={7} xs={6}   >
                            <Typography variant="caption" >{item.status ? item.status.toUpperCase() : "NEW"}</Typography>
                          </Grid>
                        </Grid>
                        <Grid container align='left'>
                          <Grid item lg={4} sm={4} xs={5}>
                            <Typography variant="caption" >{FormatText('clinic_order_date')}</Typography>
                          </Grid>
                          <Grid item lg={1} sm={1} xs={1} component='div'  >
                            <Typography >:</Typography>
                          </Grid>
                          <Grid item lg={7} sm={7} xs={6}   >
                            <Typography variant="caption" >{item.products[0].testdate}</Typography>
                          </Grid>
                        </Grid>
                        <Grid container align='left'>
                          <Grid item lg={4} sm={4} xs={5}>
                            <Typography variant="caption" >{FormatText('clinic')}</Typography>
                          </Grid>
                          <Grid item lg={1} sm={1} xs={1} component='div'  >
                            <Typography >:</Typography>
                          </Grid>
                          <Grid item lg={7} sm={7} xs={6}   >
                            <Typography variant="caption" >{item.products[0].klinik.name ? item.products[0].klinik.name : "KLINIK NAME"}</Typography>
                          </Grid>
                        </Grid>
                        <Divider style={{ margin: 10 }} />
                      </div>
                    )

                  }) : null}

                </CardContent>
              </Card>
            </div>
          ))}
        </div>
      </Hidden>

      <div style={{ height: window.screen.height - 700 }} />
    </div >
  )
}

export default SimpleTable
