import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'

import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest
} from '../../../actions/ServiceActions'
import { setClinikDetail, setCityList } from "../../../actions/PaxListActions";

import { Button, Typography, Card, Grid, FormHelperText, FormControl } from '@material-ui/core';
import CardContent from '@material-ui/core/CardContent';
import Input from '@material-ui/core/Input';
