    const { name, email, phone } = userInfo
    // let name = userInfo.name ? userInfo.name : ''
    // let email = userInfo.email ? userInfo.email : ''
    // let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      urlParams: "/cities?&citykey=a",
      customHeaders
    }

    // let clinic_parent_request = {
    //   request: {},
    //   method: "GET",
    //   serviceCode: "GET_CLINIC_PARENTS",
    //   // urlParams: "/cities?&citykey=a",
    //   customHeaders
    // }

    _setCompleteRequest(cityRequest)
    // _setCompleteRequest(clinic_parent_request)
    // _setSecondRequest(cityRequest)
