
import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import MaterialTable from 'material-table';

import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Button from '@material-ui/core/Button';
import ReactExport from 'react-data-export';
import SimpleAutocomplete from '../../../components/autocomplete/SimpleAutocomplete'

import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest, setThirdRequest
} from '../../../actions/ServiceActions'
import { setClinicRecapt, setClinikDetail, setKlinikList, setPaxProduct } from "../../../actions/PaxListActions";
import { setSnackbar } from '../../../actions/FeedbackActions';

import { yyyymmddWithParams, convertDateyyyymmdd, RupiahFormat } from '../../../utils/utils';
import FormatText from '../../../i18n'
import { SendToService } from '../../../services/ServiceClient';

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
// const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

let from = yyyymmddWithParams('lastWeek')
let dateto = yyyymmddWithParams('nextWeek')

const AllClinicReport = props => {
  const { _setOpenScreen, _setCompleteRequest, userInfo, paxList, _setSecondRequest, _setThirdRequest,
    _setClinicRecapt, _setPaxProduct, _setKlinikList } = props

  const columns = [
    { title: "ID", field: 'id', width: 50 },
    { title: FormatText('app_name'), field: 'name', },
    { title: "Product", field: 'product', width: 200 },
    { title: FormatText('clinic'), field: 'clinic', width: 200 },
    { title: "Status", field: 'status' },
    { title: FormatText('clinic_redeem_on'), field: 'redeem_on' },
    { title: FormatText('clinic_payment_date'), field: 'payment_on' },
    { title: FormatText('clinic_agent_price'), field: 'agentprice' },
    // { title: 'Pax Price', field: 'paxprice' },
  ]

  const [state, setState] = useState({
    fromdate: from,
    todate: dateto,
    simpleTable: [],
    totalPax: 0,
    agentPrice: 0,
    price: 0,
    dataset: [],
    Paid: false,
    New: false,
    finish: false,
    clinicname: "",
    productname: "",
  })

  useEffect(() => {
    // console.log("EFFECT DIPANGGIL!!! ")
    // console.log(paxList.clinicRecapt)
    let simpleTable = []
    let dataToProcess = paxList.clinicRecapt
    console.log("EFFECT DIPANGGIL!!! ", dataToProcess)
    dataToProcess.forEach(order => order.items.map(item => item.products.forEach(product => {
      let rowTable = {
        id: order.id,
        // name: product.name,
        name: item.name,
        product: product.name,
        clinic: product.klinik.name,
        status: item.status,
        redeem_on: item.redeem && item.redeem.redeem_on ? convertDateyyyymmdd(item.redeem.redeem_on) : "",// convertDateyyyymmdd(order.redeem.redeem_on),
        payment_on: order.payment && order.payment.date ? convertDateyyyymmdd(order.payment && order.payment.date) : "",// convertDateyyyymmdd(order.payment && order.payment.date),
        agentprice: product.price.nta,
        paxprice: product.price.price,
      }
      simpleTable.push(rowTable)
    })))


    // let totalPax = simpleTable.length
    let totalAgentPrice = simpleTable.reduce(function (val, element) {
      return val + parseInt(element.agentprice)
    }, 0)

    let doneTabel = []
    simpleTable.forEach(item => {
      let obj = {
        ...item,
        status: item.status === "FINISH" ? item.status = "REDEEMED" : item.status,
        agentprice: RupiahFormat(item.agentprice)
      }
      doneTabel.push(obj)
    })

    // console.log(totalAgentPrice)

    let columns = [
      { title: "ID", width: { wch: 5 } },
      { title: FormatText('app_name'), width: { wch: 30 } },
      { title: "Product", width: { wch: 30 } },
      { title: FormatText('clinic'), width: { wch: 30 } },
      { title: "Status", width: { wch: 12 } },
      { title: FormatText('clinic_redeem_on'), width: { wch: 12 } },
      { title: FormatText('clinic_payment_date'), width: { wch: 12 } },
      { title: FormatText('clinic_agent_price'), width: { wch: 12 } },
    ]
    let data = []
    simpleTable.forEach(item => {
      let dataArr = []
      dataArr.push({ value: item.id })
      dataArr.push({ value: item.name })
      dataArr.push({ value: item.product })
      dataArr.push({ value: item.clinic })
      dataArr.push({ value: item.status })
      dataArr.push({ value: item.redeem_on })
      dataArr.push({ value: item.payment_on })
      dataArr.push({ value: parseInt(item.agentprice) })
      // dataArr.push(item.paxprice)

      data.push(dataArr)
    })
    let dataset = [{ ySteps: 1, columns, data }]

    let fixedTable = doneTabel.filter(item => (item.status !== "UNCONFIRMED") && (item.payment_on))
    let totalPax = fixedTable.length

    setState(previousState => ({ ...previousState, dataset, simpleTable: fixedTable, totalPax, agentPrice: RupiahFormat(totalAgentPrice) }))
    // console.log("DATASET: ", fixedTable)
    _setOpenScreen("All Order Report")
  }, [paxList.clinicRecapt, _setOpenScreen])

  useEffect(() => {
    setState(previousState => ({ ...previousState, simpleTable: [], totalPax: 0, agentPrice: 0, }))
    const { name, email, phone } = userInfo
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    if (name && email) {
      SendToService(
        {},
        "GET",
        "GET_ALL_ORDER",
        `/list?&paymentdate_from=${from}&paymentdate_to=${dateto}`,
        customHeaders,
        null,
        null,
        response => {
          _setClinicRecapt(response.body)
          SendToService(
            {},
            "GET",
            "healthProduct",
            null,
            customHeaders,
            null,
            null,
            product => {
              _setPaxProduct(product.body)
              SendToService(
                {},
                "GET",
                "healthKlinik",
                null,
                customHeaders,
                null,
                null,
                clinic => {
                  _setKlinikList(clinic.body)
                }
              )
            }
          )
        })
    }
  }, [])


  const handleDateChange = prop => date => {
    // console.log('DateCgange: ', convertDateyyyymmdd(date))
    setState({
      ...state,
      [prop]: convertDateyyyymmdd(date),
    })
  }

  const handleExport = prop => {
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    const submitXlsForm = {
      request: {},
      method: "GET",
      serviceCode: "GET_CLINIC_XLS_REPORT",
      urlParams: `/recapt?&from_date=${state.fromdate}&to_date=${state.todate}`,
      customHeaders
    }
    _setCompleteRequest(submitXlsForm)
  }

  const handleSubmitFilter = () => {
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let paymentParam = `&paymentdate_from=${state.fromdate}&paymentdate_to=${state.todate}`
    let statusParam = (state.Paid && !state.finish)
      ? '&status=PAID' //'&redeem_status=NEW'
      : (!state.Paid && state.finish)
        ? '&status=FINISH'   // '&redeem_status=REDEEMED'
        : (state.Paid && state.finish)
          ? ""
          : ""
    let productParam = state.productname ? `&productname=${state.productname}` : ""
    let clinicParam = state.clinicname ? `&clinicname=${state.clinicname}` : ""
    const submitForm = {
      request: {},
      method: "GET",
      serviceCode: "GET_ALL_ORDER",
      urlParams: "/list?" + paymentParam + (statusParam ? statusParam : "") + productParam + clinicParam,
      customHeaders
    }
    _setCompleteRequest(submitForm)
    console.log(submitForm)
  }

  const handleChange = prop => event => {
    if (prop === 'Paid') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'New') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'finish') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Booking Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Test Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Redeem Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else {
      setState({
        ...state,
        [prop]: event.target.value,
      })
    }
  }

  const handlePickProduct = item => {
    console.log(item)
    if (item) setState({ ...state, productname: item.name })
    else setState({ ...state, productname: "" })
  }

  const handlePickClinic = item => {
    console.log(item)
    if (item) setState({ ...state, clinicname: item.name })
    else setState({ ...state, clinicname: "" })
  }

  return (

    <Grid container direction='row' justify='flex-start' alignItems='flex-start' style={{ marginBottom: 10, marginTop: 20 }} >
      <Grid item sm={2} >
        <Typography variant="subtitle2">{FormatText('clinic_payment_date')}:</Typography>
        <MuiPickersUtilsProvider utils={DateFnsUtils} >
          <Grid container style={{ marginLeft: 20 }} >
            <KeyboardDatePicker
              style={{ width: 150 }}
              size="small"
              clearable
              format="yyyy/MM/dd"
              margin="dense"
              id="date-picker-from"
              label="From"
              value={state.fromdate}
              onChange={handleDateChange('fromdate')}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </Grid>
        </MuiPickersUtilsProvider>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <Grid container style={{ marginLeft: 20 }}>
            <KeyboardDatePicker
              style={{ width: 150 }}
              clearable
              size="small"
              format="yyyy/MM/dd"
              margin="dense"
              id="date-picker-to"
              label="To"
              value={state.todate}
              onChange={handleDateChange('todate')}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </Grid>
        </MuiPickersUtilsProvider>
      </Grid>
      <Grid item sm={4} style={{ marginLeft: 20 }}  >
        <Grid
          container
          direction="column"
          justify="flex-start"
          alignItems="flex-start"
        >
          <Typography variant="subtitle2" style={{ marginTop: 25 }} >Product:</Typography>
          <SimpleAutocomplete
            small
            id="productAutocomplete"
            options={paxList.product[0] ? paxList.product : []}
            width={320}
            optionItem={'name'}
            // label="Product"
            // notShrink={true}
            // onSelect={handleSelect}
            // handleInputChange={handleInputChange}
            onPick={handlePickProduct}
          />

          <Typography variant="subtitle2" style={{ marginTop: 5 }} >Clinic:</Typography>
          <SimpleAutocomplete
            small
            id="clinicAutocomplete"
            options={paxList.klinikList[0] ? paxList.klinikList : []}
            width={320}
            optionItem={'name'}
            // label="Clinic"
            // notShrink={true}
            // onSelect={handleSelect}
            // handleInputChange={handleInputChange}
            onPick={handlePickClinic}
          />
        </Grid>
      </Grid>
      <Grid item sm={3} >
        <Typography variant="subtitle2" style={{ marginTop: 25 }}>Status:</Typography>
        <FormControl size="small" style={{ marginLeft: 15 }} fullWidth>
          <FormControlLabel
            control={<Checkbox size="small" checked={state.Paid} onChange={handleChange('Paid')} name="Paid" />}
            label="Paid"
          />
          <FormControlLabel
            control={<Checkbox size="small" checked={state.finish} onChange={handleChange('finish')} name="finish" />}
            label="Redeemed"
          />
        </FormControl>
      </Grid>
      <Grid item sm={2}  >
        <Typography variant="subtitle2">{FormatText('app_report_summary')}:</Typography>
        <Grid container direction="row"
          justify="flex-start"
          alignItems="flex-start" item style={{ marginLeft: 5, marginTop: 15 }}  >
          <Grid item sm={5}> <Typography variant='caption' >Total Order</Typography></Grid>
          <Grid item >: {state.totalPax}</Grid>
        </Grid>
        <Grid container direction="row"
          justify="flex-start"
          alignItems="flex-start" item style={{ marginLeft: 5 }} >
          <Grid item sm={5}><Typography variant='caption' >{FormatText('clinic_agent_price')}</Typography></Grid>
          <Grid item >: {state.agentPrice}</Grid>
        </Grid>
        {state.simpleTable.length < 1
          ? <Grid item style={{ marginTop: 10 }} >
            <Button disabled={state.simpleTable.length < 1 ? true : false} size='small' variant="outlined" onClick={() => handleExport(state.simpleTable)} color="primary">
              {FormatText('app_export_xls')}
            </Button>
          </Grid>
          : <Grid item style={{ marginTop: 10 }} >
            <ExcelFile
              filename={
                `OrderReport; ${state.fromdate} to ${state.todate}; ${state.Paid && !state.finish ? "PAID;" : !state.Paid && state.finish ? "REDEEMED;" : "ALL;"} ${state.productname ? " " + state.productname + ";" : ""} ${state.clinicname ? " " + state.clinicname + ";" : ""}`
              }
              element={
                <Button size='small' variant="outlined" color="primary">
                  {FormatText('app_export_xls')}
                </Button>}
            >
              <ExcelSheet dataSet={state.dataset} name="Order Report" />
            </ExcelFile>
          </Grid>}
        <Grid item style={{ marginTop: 10 }}>
          <Button size='small' variant="contained" onClick={handleSubmitFilter} color="primary">
            {FormatText('app_show_report')}
          </Button>
        </Grid>
      </Grid>

      <Grid item sm={12} style={{ marginTop: 20 }}>
        <MaterialTable
          title={FormatText('clinic_order_report')}
          columns={columns}
          data={state.simpleTable}
        />
      </Grid>
      <Grid container>
      </Grid>
    </Grid>
  )
}

const mapStateToProps = store => ({
  clientList: store.clientList.list,
  userDetail: store.clientList.detail,
  userFiltered: store.clientList.filteredList,
  userInfo: store.userInfo.info,
  paxlistStatus: store.menu.paxList,
  paxList: store.paxList
})

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)), _setSnackbar: val => dispatch(setSnackbar(val)),
    _setSecondRequest: val => dispatch(setSecondRequest(val)),
    _setThirdRequest: val => dispatch(setThirdRequest(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.urlParams))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
    _setClinicDetail: val => dispatch(setClinikDetail(val)),
    _setClinicRecapt: val => dispatch(setClinicRecapt(val)),
    _setPaxProduct: val => dispatch(setPaxProduct(val)),
    _setKlinikList: val => dispatch(setKlinikList(val)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AllClinicReport)
