
import React, { useEffect, useState, } from 'react';
import { Grid, Typography, Button, TextField } from '@material-ui/core';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import { FormValidation } from '../../../utils/FormValidation';
import { MuiPickersUtilsProvider, KeyboardDatePicker, } from '@material-ui/pickers';
import FormatText from '../../../i18n'
import { yyyymmddWithParams, convertDateyyyymmdd, datetoanydaysafter, isDateBeforeNow } from '../../../utils/utils';
import SimpleAutocomplete from '../../../components/autocomplete/SimpleAutocomplete';

let now = yyyymmddWithParams('now')

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const AddClientDialog = ({ ...props }) => {
  const { cityList, klinikList, _setCompleteRequest, _setSecondRequest, open, userInfo, item, roleUsed } = props

  const [state, setState] = useState({
    city: "",
    product: {},
    klinikList: [],
    fromdate: now,
    fromdateErrorMessage: "",
    originDate: "",
    minDate: "",
    maxValidDate: "",
    email: "",
    emailErrorMessage: "",
    reason: "",
    reasonErrorMessage: "",
    clinic_id: 0,
    order_id: 0,
    item_id: 0,
    submitErrorMessage: ""
  })

  useEffect(() => {
    let input_date = state.fromdate
    let holiday = ["2020-10-29",]
    let extra_holiday = ["2020-10-28", "2020-10-30", "2020-10-31"]
    let clinic_close_sunday = [270, 272, 273, 274, 282, 300, 8, 5, 6]
    let clinic_close_saturday = [274, 300, 5]
    let clinic_open_holiday = [270, 271, 275, 6]
    let clinic_close_extraholiday = [282, 8]
    let oldClinic = parseInt(state.product.clinic_id)
    let newClinic = parseInt(state.clinic_id)
    let d = new Date(input_date);
    let n = d.getDay();
    console.log(n, oldClinic, newClinic)
    if (newClinic) {
      if ((n === 0) && (clinic_close_sunday.includes(newClinic))) {
        setState({ ...state, submitErrorMessage: "New clinic close on Sunday, please choose another date." })
      }
      else if ((n === 6) && (clinic_close_saturday.includes(newClinic))) {
        setState({ ...state, submitErrorMessage: "New Clinic close on Saturday, please choose another date." })
      }
      else if (holiday.includes(input_date) && (!clinic_open_holiday.includes(newClinic))) {
        setState({ ...state, submitErrorMessage: "New Clinic close on Holiday, please choose another date." })
      }
      else if (extra_holiday.includes(input_date) && (clinic_close_extraholiday.includes(newClinic))) {
        setState({ ...state, submitErrorMessage: "New Clinic close on Extra Holiday, please choose another date." })
      }
      else if ((newClinic === 282) && (state.product.product_name === "PCR Covid - 19 Test")) {
        setState({ ...state, submitErrorMessage: "Product not available, please choose another clinic." })
      }
      else setState({ ...state, submitErrorMessage: "" })
    }
    else if ((n === 0) && (clinic_close_sunday.includes(oldClinic))) {
      setState({ ...state, submitErrorMessage: "Clinic close on Sunday, please choose another date." })
    }
    else if ((n === 6) && (clinic_close_saturday.includes(oldClinic))) {
      setState({ ...state, submitErrorMessage: "Clinic close on Saturday, please choose another date." })
    }
    else if (holiday.includes(input_date) && (!clinic_open_holiday.includes(oldClinic))) {
      setState({ ...state, submitErrorMessage: "Clinic close on Holiday, please choose another date." })
    }
    else if (extra_holiday.includes(input_date) && (clinic_close_extraholiday.includes(oldClinic))) {
      setState({ ...state, submitErrorMessage: "Clinic close on Extra Holiday, please choose another date." })
    }
    else setState({ ...state, submitErrorMessage: "" })

  }, [state.fromdate, state.clinic_id, state.product, state.city])

  useEffect(() => {
    let product = item
    if (product) {
      const { order_id, item_id, valid_until, payment, testdate } = product
      let minDate = isDateBeforeNow(payment.date)
        ? convertDateyyyymmdd(datetoanydaysafter(now, 1))
        : convertDateyyyymmdd(datetoanydaysafter(payment.date, 1))
      setState(prevState => ({
        ...prevState,
        klinikList,
        order_id,
        item_id,
        product,
        minDate,
        fromdate: minDate,  //   convertDateyyyymmdd(datetoanydaysafter(testdate, 1)),
        originDate: testdate,
        maxValidDate: valid_until
      }))
      console.log("BEFORE NOW? ", isDateBeforeNow(payment.date))
    }
  }, [item])

  useEffect(() => {
    if (klinikList.length === 1) setState(prevState => ({
      ...prevState,
      klinikList,
      clinic_id: klinikList[0].id
    }))
  }, [klinikList])


  const handleClose = () => {
    props.handleClose()
  };

  const handleChange = prop => e => {
    e.preventDefault()
    let val = e.target.value
    let valid = FormValidation(prop, val)
    setState({
      ...state,
      [prop]: val,
      [prop + 'ErrorMessage']: val ? valid.errorMessage : "",
      notComplete: false
    })
  }

  const handleCancel = e => {
    e.preventDefault()
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone
    const cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      params: `/cities?&citykey=a`,
      customHeaders
    }
    let cityClinicRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_CLINIC_LIST",
      urlParams: `/cities/none/clinics`,
      customHeaders
    }
    _setCompleteRequest(cityRequest)
    _setSecondRequest(cityClinicRequest)

    const { payment } = item
    let minDate = isDateBeforeNow(payment.date)
      ? convertDateyyyymmdd(datetoanydaysafter(now, 1))
      : convertDateyyyymmdd(datetoanydaysafter(payment.date, 1))
    setState({
      ...state,
      // klinikList: [],
      // product: {},
      clinic_id: 0,
      city: "",
      submitErrorMessage: "",
      minDate,
      fromdate: minDate,
    })
    props.handleClose()
  }

  const handleSave = (e) => {
    e.preventDefault()
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone

    let requestReschedule = {
      testdate: state.fromdate,
      email: state.email ? state.email : null,
      clinic: state.clinic_id ? parseInt(state.clinic_id) : null,
      reason: state.reason ? state.reason : null,
    }

    let rescheduleRequest = {
      request: requestReschedule,
      method: "PATCH",
      serviceCode: "RESCHEDULE_ORDER",
      params: `/${state.order_id}/${state.item_id}`,
      customHeaders
    }

    if (state.fromdate && state.order_id && state.item_id) {
      _setCompleteRequest(rescheduleRequest)
      props.handleClose()
    }
    else setState({ ...state, submitErrorMessage: "Form error, please check your form" })

  }

  const handleDateChange = prop => date => {
    const newDate = new Date(date);
    if (newDate > datetoanydaysafter(item.valid_until, 0)) {
      setState({
        ...state,
        [prop + 'ErrorMessage']: "Reschedule date not valid",
        [prop]: convertDateyyyymmdd(date),
      })
    } else {
      setState({
        ...state,
        [prop + 'ErrorMessage']: "",
        [prop]: convertDateyyyymmdd(date),
      })
    }
  }

  const handlePick = city => {
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone

    if (city && city.city) {
      setState({
        ...state,
        city: city.city,
        submitErrorMessage: '',
      })
      let cityRequest = {
        request: {},
        method: "GET",
        serviceCode: "GET_CITY_CLINIC_LIST",
        params: `/cities/${city.city}/clinics`,
        customHeaders
      }
      _setCompleteRequest(cityRequest)
    } else {
      let cityRequest = {
        request: {},
        method: "GET",
        serviceCode: "GET_CITY_CLINIC_LIST",
        params: `/cities/none/clinics`,
        customHeaders
      }
      _setCompleteRequest(cityRequest)
      setState({
        ...state,
        city: "",
        clinic_id: 0,
        submitErrorMessage: '',
      })
    }
  }

  const handleInputChange = val => {
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone

    let cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      params: `/cities?&citykey=${val}`,
      customHeaders
    }
    if (val) _setCompleteRequest(cityRequest)
  }

  const handlePickClinic = city => {
    if (city && city.id) {
      setState({
        ...state,
        clinic_id: city.id,
        submitErrorMessage: '',
      })
    } else {
      setState({
        ...state,
        clinic_id: 0,
        submitErrorMessage: '',
      })
    }
  }

  const handleInputChangeClinic = val => {
  }

  return (
    <Dialog fullWidth maxWidth={'xs'} open={open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle id="max-width-dialog-title" onClose={handleClose}>{props.label}</DialogTitle>
      <DialogContent dividers>
        <Grid container direction="row" justify="flex-start" alignItems='flex-start' >
          <Grid item sm={12}>
            <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Clinic</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <Typography ><b>{state.product.clinic ? state.product.clinic.name : ""}</b></Typography>
              </Grid>
            </Grid>
            <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Product Name</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <Typography ><b>{state.product.product_name}</b></Typography>
              </Grid>
            </Grid>
            <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Test Date</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <Typography ><b>{state.originDate}</b></Typography>
              </Grid>
            </Grid>
            <Grid container direction="row" justify="flex-start" alignItems="center" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Reschedule valid until</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <Typography ><b>{state.maxValidDate}</b></Typography>
              </Grid>
            </Grid>
            <Grid container direction="row" justify="flex-start" alignItems="center" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Reschedule Date</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils} >
                  <Grid container >
                    <KeyboardDatePicker
                      autoOk
                      // minDate={datetoanydaysafter(state.originDate, 1)}
                      minDate={state.minDate}
                      maxDate={state.maxValidDate}
                      error={state.fromdateErrorMessage ? true : false}
                      helperText={state.fromdateErrorMessage ? state.fromdateErrorMessage : ""}
                      style={{ width: 150 }}
                      size="small"
                      clearable
                      format="yyyy/MM/dd"
                      margin="dense"
                      id="date-picker-from"
                      value={state.fromdate}
                      onChange={handleDateChange('fromdate')}
                      KeyboardButtonProps={{
                        'aria-label': 'change date',
                      }}
                      disablePast={true}
                    />
                  </Grid>
                </MuiPickersUtilsProvider>
              </Grid>
            </Grid>
            <Grid container direction="row" justify="flex-start" alignItems="center" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >User Email</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <TextField
                  fullWidth
                  // size="small"
                  placeholder="Email"
                  onChange={handleChange('email')}
                  value={state.email}
                  error={state.emailErrorMessage ? true : false}
                  helperText={state.emailErrorMessage ? state.emailErrorMessage : ""}
                />
              </Grid>
            </Grid>
            <Grid container direction="row" justify="flex-start" alignItems="center" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Reschedule Reason</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                <TextField
                  fullWidth
                  // size="small"
                  placeholder="Reschedule Reason"
                  multiline
                  onChange={handleChange('reason')}
                  value={state.reason}
                  error={state.reasonErrorMessage ? true : false}
                  helperText={state.reasonErrorMessage ? state.reasonErrorMessage : ""}
                />
              </Grid>
            </Grid>

            <Grid style={{ visibility: roleUsed === 'voltras.admin' ? 'visible' : 'hidden' }}
              container direction="row" justify="flex-start" alignItems="center" >
              <Grid item lg={5} sm={5} xs={5}>
                <Typography >Change Clinic</Typography>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
                <Typography >:</Typography>
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                {}
                <SimpleAutocomplete
                  id="changeClinicChooseCity"
                  options={cityList[0] ? cityList : []}
                  optionItem={'city'}
                  notShrink={true}
                  handleInputChange={handleInputChange}
                  onPick={handlePick}
                />
              </Grid>
            </Grid>
            <Grid style={{ visibility: roleUsed === 'voltras.admin' ? 'visible' : 'hidden' }}
              container direction="row" justify="flex-start" alignItems="center" >
              <Grid item lg={5} sm={5} xs={5}>
              </Grid>
              <Grid item lg={1} sm={1} xs={1} component='div'  >
              </Grid>
              <Grid item lg={6} sm={6} xs={6}>
                {
                  (klinikList[0]) && (klinikList[0].name) && (klinikList.length > 1)
                    ? <SimpleAutocomplete
                      id="changeClinicChooseClinic"
                      options={klinikList}
                      optionItem={'name'}
                      notShrink={true}
                      handleInputChange={handleInputChangeClinic}
                      onPick={handlePickClinic}
                    />
                    : (klinikList[0]) && (klinikList[0].name) && (klinikList.length === 1)
                      ? <TextField focused={false} value={klinikList[0].name} fullWidth />
                      : null
                }
              </Grid>

            </Grid>

          </Grid>
        </Grid>
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ marginTop: 5, marginBottom: 5 }}>
          <Typography color="error" style={{ visibility: state.submitErrorMessage ? 'visible' : 'hidden' }} variant='subtitle2' >
            {state.submitErrorMessage}
          </Typography>
          <Grid container item direction="row" justify='center' alignItems='center' >
            <Button variant='outlined' color="primary" onClick={handleCancel} style={{ marginRight: 5 }}>
              {FormatText('app_cancel')}
            </Button>
            <Button
              disabled={state.submitErrorMessage ? true : false}
              variant='contained'
              color="primary"
              onClick={handleSave}
              style={{ marginLeft: 5 }}
            >
              Reschedule
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
      {console.log("STATE: ", state)}
    </Dialog>
  )
}

export default AddClientDialog
