import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import MaterialTable from 'material-table';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Button from '@material-ui/core/Button';
import ReactExport from 'react-data-export';

import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest,
} from '../../../actions/ServiceActions'
import { setClinicRecapt, setClinikDetail } from "../../../actions/PaxListActions";
import { setSnackbar } from '../../../actions/FeedbackActions';

import { yyyymmddWithParams, convertDateyyyymmdd, RupiahFormat } from '../../../utils/utils';
import FormatText from '../../../i18n'

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
// const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

let from = yyyymmddWithParams('lastWeek')
let dateto = yyyymmddWithParams('nextWeek')

const ClinicReport = props => {
  const { _setOpenScreen, _setCompleteRequest, userInfo, paxList, roleUsed, _setClinicRecapt } = props

  const [columns, setColumns] = useState([])

  const [state, setState] = useState({
    fromdate: from,
    todate: dateto,
    simpleTable: [],
    totalPax: 0,
    agentPrice: 0,
    price: 0,
    dataset: []
  })

  const initOne = () => {
    _setClinicRecapt([])
    const { name, email, phone } = userInfo
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    const submitFormRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CLINIC_REPORT",
      urlParams: `/recapt?&from_date=${state.fromdate}&to_date=${state.todate}`,
      customHeaders
    }
    _setCompleteRequest(submitFormRequest)
    _setOpenScreen("Redeemed Order")
  }

  useEffect(initOne, [])

  useEffect(() => {
    const tableColumn = [
      { title: "ID", field: 'id', width: 50 },
      { title: FormatText('app_name'), field: 'name', },   //   width: 300 },
      { title: FormatText('clinic'), field: 'clinic' },
      { title: FormatText('clinic_redeem_on'), field: 'redeem_on' },
      { title: FormatText('clinic_payment_date'), field: 'payment_on' },
      { title: "NTA", field: 'nta' },
      { title: "NTSA", field: 'ntsa' },
      { title: FormatText('clinic_price'), field: 'agentprice' },
    ]

    let newColumn = [...tableColumn]
    if (roleUsed === 'voltras.admin') setColumns(newColumn)
    else setColumns(newColumn.filter(column => column.field !== "agentprice" && column.field !== "ntsa"))
  }, [roleUsed])

  useEffect(() => {
    let simpleTable = []
    let dataToProcess = paxList.clinicRecapt
    dataToProcess.forEach(order => order.items.forEach(item => item.products.forEach(product => {
      let rowTable = {
        id: order.id,
        name: product.name,
        clinic: product.klinik.name,
        redeem_on: item.redeem && item.redeem.redeem_on ? convertDateyyyymmdd(item.redeem.redeem_on) : "",
        payment_on: order.payment && order.payment.date ? convertDateyyyymmdd(order.payment && order.payment.date) : "",
        nta: product.price.nta,
        ntsa: product.price.ntsa,
        agentprice: product.price.price,
      }
      simpleTable.push(rowTable)
    })))

    let totalPax = simpleTable.length
    let totalAgentPrice = simpleTable.reduce(function (val, element) {
      return val + parseInt(element.nta)
    }, 0)

    let doneTabel = []
    simpleTable.forEach(item => {
      let obj = {
        ...item,
        nta: RupiahFormat(item.nta),
        ntsa: RupiahFormat(item.ntsa),
        agentprice: RupiahFormat(item.agentprice),
      }
      doneTabel.push(obj)
    })

    let columns = [
      { title: "ID", width: { wch: 5 } },
      { title: FormatText('app_name'), width: { wch: 30 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
      { title: FormatText('clinic'), width: { wch: 30 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
      { title: FormatText('clinic_redeem_on'), width: { wch: 20 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
      { title: FormatText('clinic_payment_date'), width: { wch: 20 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
      { title: 'NTA', width: { wch: 12 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
      { title: 'NTSA', width: { wch: 12 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
      { title: FormatText('clinic_price'), width: { wch: 12 }, style: { font: { bold: true }, alignment: { horizontal: 'center' } }, },
    ]
    let newColumns = []
    if (roleUsed !== 'voltras.admin') newColumns = columns.filter(column => column.title !== FormatText('clinic_price') && column.title !== "NTSA")
    else newColumns = columns
    let data = []
    simpleTable.forEach(item => {
      let dataArr = []
      dataArr.push({ value: item.id })
      dataArr.push({ value: item.name })
      dataArr.push({ value: item.clinic })
      dataArr.push({ value: item.redeem_on })
      dataArr.push({ value: item.payment_on })
      dataArr.push({ value: parseInt(item.nta) })
      if (roleUsed === 'voltras.admin') {
        dataArr.push({ value: parseInt(item.ntsa) })
        dataArr.push({ value: parseInt(item.agentprice) })
      }
      data.push(dataArr)
    })
    let dataset = [{ ySteps: 1, columns: newColumns, data }]

    setState(prevState => ({ ...prevState, dataset, simpleTable: doneTabel, totalPax, agentPrice: RupiahFormat(totalAgentPrice) }))
  }, [paxList.clinicRecapt, roleUsed])

  const handleDateChange = prop => date => {
    // console.log('DateCgange: ', convertDateyyyymmdd(date))
    setState({
      ...state,
      [prop]: convertDateyyyymmdd(date),
    })
  }

  // const handleChange = prop => event => {
  //   console.log('Change: ', prop)
  // }

  const handleExport = prop => {
    // console.log('Change: ', prop)
    // history.push('/KExcelReport')
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    const submitXlsForm = {
      request: {},
      method: "GET",
      serviceCode: "GET_CLINIC_XLS_REPORT",
      urlParams: `/recapt?&from_date=${state.fromdate}&to_date=${state.todate}`,
      customHeaders
    }
    _setCompleteRequest(submitXlsForm)
  }

  const handleSubmitFilter = () => {
    //GET health/admin/report/recapt?from_date=2020-01-01&to_date=2020-09-01
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    const submitForm = {
      request: {},
      method: "GET",
      serviceCode: "GET_CLINIC_REPORT",
      urlParams: `/recapt?&from_date=${state.fromdate}&to_date=${state.todate}`,
      customHeaders
    }
    _setCompleteRequest(submitForm)
  }

  return (

    <Grid container direction='row' justify='flex-start' alignItems='flex-start' style={{ marginBottom: 10, marginTop: 20 }} >
      {/* {console.log("TABLE DONE: ", state.simpleTable)} */}
      {/* <Grid item sm={4} >
                <Grid item>
                  <Typography variant="subtitle2">Name:</Typography>
                  <FormControl style={{ width: 300 }}  >
                    <FormGroup >
                      <Input
                        error={state.emailErrorMessage ? true : false}
                        id="standard-full-width"
                        type='text'
                        placeholder='Name'
                        onChange={handleChange('name')}
                        endAdornment={
                          <InputAdornment position="end">
                            <AccountBoxRounded />
                          </InputAdornment>
                        }
                      />
                    </FormGroup>
                  </FormControl>
                </Grid>
                <Grid item> <br />
                </Grid>

                <Grid item>
                  <Typography style={{ marginBottom: 7 }} variant="subtitle2">Klinik Name:</Typography>
                  {
                    this.props.paxList.klinikList.length === 1
                      ? <TextField id="basic-klinik" value={this.props.paxList.klinikList[0].name} style={{ width: 300 }} />
                      : this.props.paxList.klinikList.length === 0
                        ? <TextField id="basic-klinik" disabled style={{ width: 300 }} />
                        : <SimpleAutocomplete
                          options={this.props.paxList.klinikList[0] ? this.props.paxList.klinikList : [{ name: '' }]}
                          onPick={v => console.log(v)}
                          width={300}
                          optionItem={'name'}
                        />
                  }

                </Grid>
              </Grid> */}
      <Grid item sm={4} >
        <Typography variant="subtitle2">{FormatText('clinic_payment_date')}:</Typography>
        <MuiPickersUtilsProvider utils={DateFnsUtils} >
          <Grid container style={{ marginLeft: 20 }} >
            <KeyboardDatePicker
              size="small"
              clearable
              format="yyyy/MM/dd"
              margin="dense"
              id="date-picker-from"
              label="From"
              value={state.fromdate}
              onChange={handleDateChange('fromdate')}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </Grid>
        </MuiPickersUtilsProvider>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <Grid container style={{ marginLeft: 20 }}>
            <KeyboardDatePicker
              clearable
              size="small"
              format="yyyy/MM/dd"
              margin="dense"
              id="date-picker-to"
              label="To"
              value={state.todate}
              onChange={handleDateChange('todate')}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </Grid>
        </MuiPickersUtilsProvider>
      </Grid>
      {/* <Grid item sm={3} >
        <FormControl style={{ marginLeft: 15 }} fullWidth>
          <Typography variant="subtitle2">Status:</Typography>
          <FormControl>
            <FormControlLabel
              control={<Checkbox checked={state.New} onChange={handleChange('New')} name="New" />}
              label="NEW"
            />
            <FormControlLabel
              control={<Checkbox checked={state.finish} onChange={handleChange('finish')} name="finish" />}
              label="FINISH"
            />
          </FormControl>
        </FormControl>
      </Grid> */}
      <Grid item sm={2} style={{ marginTop: 10 }}  >
        <Grid item><br /></Grid>
        <Grid
          container
          direction="column"
          justify='center'
          alignItems="center">
          {state.simpleTable.length < 1
            ? <Grid item>
              <Button disabled={state.simpleTable.length < 1 ? true : false} size='small' variant="outlined" onClick={() => handleExport(state.simpleTable)} color="primary">
                {FormatText('app_export_xls')}
              </Button>
            </Grid>
            : <Grid item>
              <ExcelFile
                filename={`OrderReport ${state.fromdate} to ${state.todate}`}
                element={
                  <Button size='small' variant="outlined" color="primary">
                    {FormatText('app_export_xls')}
                  </Button>}
              >
                <ExcelSheet dataSet={state.dataset} name="Order Report" />
              </ExcelFile>
            </Grid>}


          <Grid> <br /></Grid>
          <Grid item>
            <Button size='small' variant="contained" onClick={handleSubmitFilter} color="primary">
              {FormatText('app_show_report')}
            </Button>
          </Grid>
        </Grid>
        {/* <Typography style={{ marginLeft: 20 }} variant="subtitle2">Export to xls:</Typography>
                <Grid
                  container
                  direction="column"
                  justify="center"
                  alignItems="center">
                  <Grid item><br /><br /></Grid>
                  <Grid item>
                    <Button variant="contained" onClick={handleExport} color="primary">
                      Export
                    </Button>
                  </Grid>
                  <Grid item ><br /> <br /></Grid>
                </Grid> */}
      </Grid>

      <Grid item sm={1} ></Grid>

      <Grid item sm={5}  >
        <Typography variant="subtitle2">{FormatText('app_report_summary')}:</Typography>
        <Grid container item style={{ marginLeft: 20, marginTop: 15 }}  >
          <Grid item sm={3}> <Typography variant='caption' >Total Order</Typography></Grid>
          <Grid item sm={3}>: {state.totalPax}</Grid>
        </Grid>
        <Grid container item style={{ marginLeft: 20 }} >
          <Grid item sm={3}><Typography variant='caption' >{FormatText('clinic_agent_price')}</Typography></Grid>
          <Grid item sm={3}>: {state.agentPrice}</Grid>
        </Grid>
        {/* <Grid container item style={{ marginLeft: 20 }} >
          <Grid item sm={3}><Typography variant='caption' >Price</Typography></Grid>
          <Grid item sm={3}>: "3434"</Grid>
        </Grid> */}


      </Grid>



      <Grid item sm={12} style={{ marginTop: 20 }}>
        <MaterialTable
          title={FormatText('clinic_order_report')}
          columns={columns}
          data={state.simpleTable}
        />
      </Grid>
      <Grid container>
        {/* <div style={{ height: window.screen.height - 700 }} /> */}
      </Grid>
    </Grid>
  )
}

const mapStateToProps = store => ({
  clientList: store.clientList.list,
  userDetail: store.clientList.detail,
  userFiltered: store.clientList.filteredList,
  userInfo: store.userInfo.info,
  paxlistStatus: store.menu.paxList,
  paxList: store.paxList,
  roleUsed: store.userInfo.roleUsed,
})

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)), _setSnackbar: val => dispatch(setSnackbar(val)),
    _setSecondRequest: val => dispatch(setSecondRequest(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.urlParams))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
    _setClinicDetail: val => dispatch(setClinikDetail(val)),
    _setClinicRecapt: val => dispatch(setClinicRecapt(val)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClinicReport)
